<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-buttons slot="start">
          <ion-back-button default-href="/family-list"></ion-back-button>
        </ion-buttons>
        <ion-title>編輯寵物資料</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content class="family-list">
      <ion-grid>
        <ion-row style="justify-content: center">
          <ion-col size="4">
            <ion-img :src="imgUrl" alt="未上傳寵物圖片"/>
          </ion-col>
        </ion-row>
        <ion-row>
          <ion-col size="12">
            <ion-item class="input">
              <ion-label>　　寵物圖片　　</ion-label>
              <ion-input accept="image/*" @change="imgFileChange" type="file"></ion-input>
            </ion-item>
          </ion-col>
          <ion-col size="12">
            <ion-item class="input">
              <ion-label>　　寵物名稱　　</ion-label>
              <ion-input v-model.trim="name"></ion-input>
            </ion-item>
          </ion-col>
          <ion-col size="12">
            <ion-item class="input select">
              <ion-label>　　曆別　　</ion-label>
              <ion-select interface="popover" v-model="calendar_type">
                <ion-select-option value="1">國曆</ion-select-option>
                <ion-select-option value="2">農曆</ion-select-option>
              </ion-select>
            </ion-item>
          </ion-col>
          <ion-col size="12">
            <ion-item class="input select">
              <ion-label>　　寵物生日　　</ion-label>
              <ion-datetime displayFormat="YYYY-MM-DD" v-model="birthday"></ion-datetime>
            </ion-item>
          </ion-col>
          <ion-col size="12">
            <ion-button class="button-3" @click="save">儲存</ion-button>
          </ion-col>
        </ion-row>
      </ion-grid>
    </ion-content>
    <ion-footer>
      <menu-bar></menu-bar>
    </ion-footer>
  </ion-page>
</template>

<script>
import {
  queryFamily,
  saveFamily,
  getImgSrc,
} from "../api";

import {
  IonBackButton,
  IonButton,
  IonButtons,
  IonCol,
  IonContent,
  IonDatetime,
  IonFooter,
  IonGrid,
  IonHeader,
  IonInput,
  IonItem,
  IonLabel,
  IonPage,
  IonRow,
  IonSelect,
  IonSelectOption,
  IonTitle,
  IonToolbar
} from "@ionic/vue";

import MenuBar from "@/views/MenuBar.vue";

export default {
  components: {
    IonBackButton,
    IonButton,
    IonButtons,
    IonCol,
    IonContent,
    IonDatetime,
    IonFooter,
    IonGrid,
    IonHeader,
    IonInput,
    IonItem,
    IonLabel,
    IonPage,
    IonRow,
    IonSelect,
    IonSelectOption,
    IonTitle,
    IonToolbar,
    MenuBar
  },
  data() {
    return {
      id: this.$route.params.id,
      name: "",
      calendar_type: "",
      birthday: "",
      image: '',
      imgUrl: '',
    };
  },
  methods: {
    query() {
      queryFamily(this.id, (result) => {
        let family = result.family;
        this.name = family.name;
        this.calendar_type = `${family.calendar_type}`;
        this.birthday = family.birthday;
        this.image = family.image;
      });
    },
    save() {
      saveFamily({
        id: this.id,
        name: this.name,
        calendar_type: this.calendar_type,
        birthday: this.birthday.split("T")[0],
        image: this.image,
      }, () => {
        this.$router.go(-1);
      });
    },
    imgFileChange(e) {
      const file = e.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.image = reader.result;
      };
    },
  },
  mounted() {
    this.query();
  },
  watch: {
    $route(to) {
      if (to.name === "Family") {
        this.id = to.params.id;
        this.query();
      }
    },
    image() {
      this.imgUrl = getImgSrc(this.image)
    },
  },
};
</script>

<style scoped>
ion-grid {
  margin: 20px 20px 0 20px;
}
</style>
